.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.Button-border {
  background-color: teal;
  width: 140px;
  height: 80px;
  display: flex;
  float: left;
  margin-top: 250px;
}
.Button-inner {
  background-color: chocolate;
  color: chartreuse;
  width: 120px;
  height: 60px;
  font-size: 20px;
  margin: auto;
}
.Button-inner:hover {
  background-color: coral;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.DudeBase {
  transform: translateY(30px) scaleX(-1);
  position: absolute;
  width: 300px;
  height: 500px;
}

.ToiletUpper {
  height: 350px;
  background-image: linear-gradient(#ddd, #666);
}

.ToiletLower {
  height: 150px;
  background-image: linear-gradient(#777, #999);
}

.ImageHolder {
  position: absolute;
  top: 150px;
  z-index: 10;
}

.PissPuddle {
  position: absolute;
  top: 408px;
  width: 160px;
  height: 60px;
  border-radius: 50%;
  background-image: linear-gradient(#d7cf35, #9ea428);
  left: 100px;
  z-index: 1;
}

.ShitStain {
  position: absolute;
  border-radius: 50%;
  background-image: linear-gradient(#752f0f, #a24013);
  z-index: 5;
}

.Door-grid {
  display: grid;
  grid-template-columns: 1fr 8fr 1fr;
  grid-template-rows: 1fr 8fr 1fr 8fr 1fr;
  width: 300px;
  height: 500px;
  background-color: #793c1f;
  transform-origin: left center;
}
.Door-anim {
  animation-duration: 2000ms, 2000ms;
  animation-delay: 0ms, 2000ms;
  animation-name: open, close;
  animation-iteration-count: 1, 1;
}
@keyframes close {
  0% {
    transform: rotateY(-80deg);
  }
  100% {
    transform: rotateY(0deg);
  }
}
@keyframes open {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(-80deg);
  }
}

.Grid-wrap {
  perspective: 1000px;
  z-index: 100;
}

.Door-wrapper {
  float: left;
  padding: 20px;
}

.Door-lower {
  grid-column: 2;
  grid-row: 2;
  background-color: #8e401c;
  border-width: 5px;
  border-style: outset;
  border-color: #a84b20;
}

.Door-upper {
  grid-column: 2;
  grid-row: 4;
  background-color: #8e401c;
  border-width: 5px;
  border-style: outset;
  border-color: #a84b20;
}

.Door-below {
  width: 300px;
  height: 200px;
}

.Door-knob {
  grid-row: 3;
  grid-column: -2/-1;
  background: yellow;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border-width: 3px;
  border-style: solid;

  border-top-color: yellow;
  border-left-color: yellow;
  border-right-color: orange;
  border-bottom-color: orange;
}

.Occupation-outer {
  background-color: teal;
  width: 70px;
  height: 30px;
  margin: auto;
  display: flex;
  font-size: 18px;
}

.Occupation-inner-vacant {
  width: 60px;
  height: 24px;
  background-color: green;
  color: lightgreen;
  margin: auto;
}

.Occupation-inner-occupied {
  width: 60px;
  height: 24px;
  background-color: red;
  color: pink;
  margin: auto;
}
